module.exports = "## [dojox/mvc/getStateful](https://dojotoolkit.org/reference-guide/1.10/dojox/mvc/getStateful.html)\r\n\r\n获取状态值 用于双向绑定\r\n\r\n```javascript\r\nvar stateful = getStateful({ name: \"\" , address: \"\" , age: \"\" });\r\n```\r\n\r\n修改值\r\n\r\n```javascript\r\nstateful.set(\"name\", \"123456\");\r\n//或\r\nstateful.set({name: \"123456\" ,address: \"abc\"});\r\n```\r\n**错误方式** 通过赋值方式 双向绑定会失效\r\n```javascript\r\nstateful.name = \"123456\";\r\n```\r\n\r\n\r\n## [dojox/mvc/getPlainValue](https://dojotoolkit.org/reference-guide/1.10/dojox/mvc/getPlainValue.html)\r\n\r\n获取原始值\r\n\r\n状态值会多一些装饰方法，提交数据时需要获取原始值\r\n\r\n```javascript\r\nvar plainValue = getPlainValue(stateful);\r\n```\r\n\r\n## [dojox/mvc/at](https://dojotoolkit.org/reference-guide/1.10/dojox/mvc/at.html)\r\n\r\n绑定属性\r\n\r\n* 指定对象\r\n\r\n```javascript\r\nat(this, \"props\")\r\n```\r\n\r\n* 指定引用\r\n\r\n`rel:` 向上查找组件的 `target` 值作为引用对象\r\n\r\n```javascript\r\nat(\"rel:\", \"props\")\r\n```\r\n\r\n* 单向输入\r\n\r\n```javascript\r\nat(\"rel:\", \"props\").direction(at.from)\r\n```\r\n\r\n* 单向输出\r\n\r\n```javascript\r\nat(\"rel:\", \"props\").direction(at.to)\r\n```\r\n\r\n* 格式化绑定\r\n\r\n```javascript\r\nat(\"rel:\", \"props\").transform({format:function(value){ return value.replace(/(\\d{3})(\\d{4})/, '$1 $2 ') },parse:function(value){ return parseInt(value) }})\r\n```\r\n\r\n## [dojox/mvc/Output](https://dojotoolkit.org/reference-guide/1.10/dojox/mvc/Output.html)\r\n\r\n展现数据\r\n\r\n```html\r\n<span data-dojo-type=\"dojox/mvc/Output\" data-dojo-props=\"value: at(model, 'balance')\"></span>\r\n```\r\n\r\n```html\r\n<span data-dojo-type=\"dojox/mvc/Output\" data-dojo-props=\"value: at(model, 'balance')\">\r\n    Your balance is: ${this.value}\r\n</span>\r\n```\r\n\r\n## [dojox/mvc/Repeat](https://dojotoolkit.org/reference-guide/1.10/dojox/mvc/Repeat.html)\r\n\r\n迭代绑定\r\n\r\n## [dojox/mvc/Group](https://dojotoolkit.org/reference-guide/1.10/dojox/mvc/Group.html)\r\n\r\n层级连接点\r\n\r\n```javascript\r\nthis.model.data.name\r\n```\r\n\r\n不使用 `dojox/mvc/Group` 如果 data 为 `undefined` 下面这种方式会报错\r\n\r\n```html\r\n<div data-dojo-type=\"dijit/form/TextBox\" data-dojo-props=\"value:at(this.model.data,'name')\"></div>\r\n```\r\n\r\n```html\r\n<div data-dojo-type=\"dojox/mvc/Group\" data-dojo-props=\"target: at(this, 'model')\">\r\n    <div data-dojo-type=\"dojox/mvc/Group\" data-dojo-props=\"target: at('rel:', 'data')\">\r\n        <div data-dojo-type=\"dijit/form/TextBox\" data-dojo-props=\"value:at('rel:','name')\"></div>\r\n    </div>\r\n</div>\r\n```\r\n\r\n**示例代码**\r\n```code/MVVM.html\r\n```\r\n\r\n```code/MVVM.js\r\n```"

